@import url(https://fonts.googleapis.com/css2?family=Glory&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Glory&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.header {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: flex-start;
}

.title {
  color: #989eaa;
  font-size: 20px;
  font-weight: bold;
  text-align: left;
}

.date {
  font-size: 15px;
}

.date h1 {
  margin-bottom: 0;
}

.date div {
  color: #989eaa;
  font-weight: bold;
  font-size: 22px;
  text-align: right;
}

.wordCard {
  display: flex;
  flex-direction: column;
  color: black;
}

.wordCard h1 {
  text-align: center;
  font-size: 90px;
  font-weight: bolder;

  margin-bottom: 0px;
}

.wordCard h3 {
  font-style: italic;
  font-weight: normal;
  text-align: center;
  font-size: 20px;
  padding-top: 15px;
  margin-top: 0px;
}

.wordCard h4 {
  text-align: center;
  font-size: 30px;

  padding-top: 50px;
  padding-bottom: 50px;
}

.defnition {
  text-align: center;
  font-size: 50px;
  position: relative;
  padding-bottom: 20px;
  padding-top: 20px;
}
.defnition::before {
  content: "";
  width: 50%;
  position: absolute;
  border-top: solid black 5px;
  left: 25%;
  top: 0;
}

button.genericButton {
  background-color: rgb(251, 194, 18);
  padding: 10px;
  align-self: center;
  font-weight: bold;
  color: #101820ff;
  width: 200px;
  border: none;
  cursor: pointer;
  margin-top: 10px;
  margin-bottom: 10px;
}

