} */

.box button {
  cursor: pointer;
  width: 40px;
  height: 40px;
  background-color: white;
  justify-content: center;
  border: none;
}

table {
  border-spacing: 10px;
  background: white;
  border-radius: 12px;
  padding: 40px;
}

.audioButtonReview {
  border: none;
  cursor: pointer;
  font-size: 45px;
  height: 10px;
  background: none;
}

.audioButton {
  border: none;
  cursor: pointer;
  font-size: 30px;
  background: none;
}

.reviewView {
  font-family: "Glory", sans-serif;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.header {
  display: flex;
  flex-direction: row;
}

.backButton {
  text-align: left;
  align-self: flex-start;
  font-size: 50px;
  margin-bottom: 0px;
  padding-bottom: 0px;
  border: none;
  background: none;
  cursor: pointer;
}

.reviewView p {
  margin-top: 0px;
  text-align: center;
  color: #3a5180;
  font-size: 50px;
  font-weight: bold;
}

.reviewView .wordListItem {
  font-family: "Glory", sans-serif;
  font-size: 30px;
  width: 800px;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

body {
  background-color: rgb(251, 194, 18);
  font-family: "Glory", sans-serif;
}

.frame {
  display: flex;
  flex-direction: column;
  background-color: rgb(235, 235, 235);
  margin: 20px 60px;
  padding: 20px 40px;
}

